// src/Services/cloudinaryService.ts

export const CLOUD_NAME = 'dmplej3ht'; // Thay b·∫±ng cloudName c·ªßa b·∫°n
export const UPLOAD_PRESET = 'petapp_unsigned'; // Thay b·∫±ng upload preset b·∫°n ƒë√£ t·∫°o tr√™n Cloudinary

/**
 * Upload 1 file l√™n Cloudinary
 * @param file File c·∫ßn upload
 * @returns URL ·∫£nh sau upload ho·∫∑c null n·∫øu l·ªói
 */
export const uploadImageToCloudinary = async (file: File): Promise<string | null> => {
  try {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('upload_preset', UPLOAD_PRESET);

    const response = await fetch(`https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`, {
      method: 'POST',
      body: formData,
    });

    const data = await response.json();

    if (data.secure_url) {
      return data.secure_url;
    } else {
      console.error('Cloudinary upload failed:', data);
      return null;
    }

  } catch (error) {
    console.error('Error uploading to Cloudinary:', error);
    return null;
  }
};
