import React, { JSX } from 'react'; // th√™m useState
import { Menu, message } from 'antd';
import { ArrowLeftOutlined, SendOutlined } from '@ant-design/icons';
import './menuAdd.css';
import { useNavigate } from 'react-router-dom';
import { collection, addDoc } from 'firebase/firestore';
import { db } from "../../../Firebase/FirebaseConfig"; // ƒë√£ c√≥ s·∫µn db
import { FormData } from '../AddRequest/addRequest';
import dayjs from 'dayjs';
import { uploadImageToCloudinary } from '../../../Services/cloudinaryService';
import { RcFile } from 'antd/es/upload';

interface MenuAddProps {
    formData: FormData;
    setFormData: React.Dispatch<React.SetStateAction<FormData>>;
    fileList: RcFile[];
    setFileList: React.Dispatch<React.SetStateAction<RcFile[]>>;
    setLoading: React.Dispatch<React.SetStateAction<boolean>>; 
    listOfUserId: string[]; 
}

function MenuAdd({ formData, setFormData, fileList, setFileList, setLoading, listOfUserId}: MenuAddProps): JSX.Element {
    const navigate = useNavigate();

    const handleReturn = () => {
        navigate("/");
    };

    const handleSubmit = async () => {
        const createdAt = dayjs().format('YYYY-MM-DDTHH:mm:ss');
        const userEmail = localStorage.getItem('email') || '';
        setLoading(true);
    
        try {
            if (formData.notificationType === 'violation') {
                const {
                    petId,
                    violationLocation,
                    violationTime,
                    description,
                    notes,
                    ownerId
                } = formData;
    
                if (!petId || !violationLocation || !violationTime || !description || !ownerId) {
                    message.error("Vui l√≤ng ƒëi·ªÅn ƒë·∫ßy ƒë·ªß c√°c tr∆∞·ªùng b·∫Øt bu·ªôc cho vi ph·∫°m!");
                    setLoading(false);
                    return;
                }
    
                const uploadedUrls = fileList.length > 0
                    ? await Promise.all(fileList.map(uploadImageToCloudinary))
                    : [];
    
                const violationData = {
                    petId,
                    ownerId,
                    violationLocation,
                    violationTime,
                    description,
                    notes: notes || "",
                    createdAt,
                    status: 'Pending',
                    notificationBy: userEmail,
                    attachments: uploadedUrls,
                };
    
                await addDoc(collection(db, 'violations'), violationData);
                message.success("G·ª≠i th√¥ng b√°o vi ph·∫°m th√†nh c√¥ng!");
                navigate("/");
            }
    
            else if (formData.notificationType === 'vaccination') {
                const {
                    vaccineType,
                    vaccineLocation,
                    cost,
                    vaccineFrom,
                    vaccineTo,
                    notes,
                    issuingAuthority
                } = formData;
    
                if (!vaccineType || !vaccineLocation || !cost || !vaccineFrom || !vaccineTo || !issuingAuthority) {
                    message.error("Vui l√≤ng ƒëi·ªÅn ƒë·∫ßy ƒë·ªß c√°c tr∆∞·ªùng b·∫Øt bu·ªôc cho ti√™m ph√≤ng!");
                    setLoading(false);
                    return;
                }
    
                const vaccinationData = {
                    createdAt,
                    vaccineType,
                    vaccineLocation,
                    cost,
                    timeFrom: vaccineFrom,
                    timeTo: vaccineTo,
                    notes: notes || "",
                    issuingAuthority,
                    // status: 'Pending',
                    notificationBy: userEmail,
                    senderTo: listOfUserId.map(userId => ({ 
                        userId, 
                        status: 'Pending'
                    })),
                };
    
                await addDoc(collection(db, 'vaccinations'), vaccinationData);
                message.success("G·ª≠i th√¥ng b√°o ti√™m ph√≤ng th√†nh c√¥ng!");
                navigate("/");
            }
    
            else {
                message.error("Lo·∫°i th√¥ng b√°o kh√¥ng h·ª£p l·ªá!");
            }
        } catch (error) {
            console.error("L·ªói khi g·ª≠i y√™u c·∫ßu:", error);
            message.error("G·ª≠i y√™u c·∫ßu th·∫•t b·∫°i!");
        } finally {
            setLoading(false);
        }
    };    

    return (
        <div className='menu-detail-request'>
            <Menu mode="horizontal" className='fixed-menu'>
                <Menu.Item onClick={handleReturn} key="return" icon={<ArrowLeftOutlined />}>
                    Quay l·∫°i
                </Menu.Item>
                <Menu.Item onClick={handleSubmit} key="submit" icon={<SendOutlined />}>
                    G·ª≠i y√™u c·∫ßu
                </Menu.Item>
            </Menu>
        </div>
    );
}

export default MenuAdd;
