import MenuAdd from '../MenuAdd/menuAdd';
import SendApprover from '../SendApprover/sendApprover';
import RequestLayout from '../../RequestLayout';
import { Col, Input, Row, Form, Select, DatePicker, Spin, Alert, Upload, Button, message} from 'antd';
import './addRequest.css'
import '../SendApprover/sendApprover.css'
import { ChangeEvent, JSX, useEffect, useState } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import { RcFile } from 'antd/es/upload';
import { db } from "../../../Firebase/FirebaseConfig"; 
import { collection, query, where, onSnapshot, doc } from 'firebase/firestore';
import { DocumentSnapshot, DocumentData } from 'firebase/firestore';
import { UploadOutlined } from '@ant-design/icons';
import { UploadFile } from 'antd/lib/upload';

export interface FormData {
    notificationType?: string;
    petId?: string;
    violationLocation?: string;
    violationTime?: string;
    description?: string;
    notes?: string;
    [key: string]: any;
}

function AddRequest(): JSX.Element {

    //set layout
    const profile = false;

    const [notificationOptions] = useState([
        { value: 'vaccination', label: 'Ti√™m ph√≤ng' },
        { value: 'violation', label: 'Vi ph·∫°m' }
      ]);
    const [loading, setLoading] = useState<boolean>(false);
    const [fileList, setFileList] = useState<RcFile[]>([]);
    const [applyNote, setApplyNote] = useState<boolean>(false);
    const [listOfUserId, setListOfUserId] = useState<string[]>([]);
    const [petOptions, setPetOptions] = useState<{ value: string; label: string }[]>([]);

    //set initial formData
    const [ownerInfo, setOwnerInfo] = useState<{ fullName: string; phone: string }>({
        fullName: "",
        phone: ""
    });
    //set formData post Api to server 
    const [formData, setFormData] = useState<FormData>({});

    useEffect(() => {
        let unsubscribe: () => void;
      
        const fetchActivePets = async () => {
          try {
            setLoading(true);
            const q = query(
              collection(db, 'pets'),
              where('status', '==', 'Active')
            );
      
            // Thi·∫øt l·∫≠p real-time listener
            unsubscribe = onSnapshot(q, (querySnapshot) => {
              const options = querySnapshot.docs.map(doc => ({
                value: doc.id,
                label: `${doc.data().name} <${doc.id}>`
              }));
              setPetOptions(options);
              setLoading(false);
            }, (error) => {
              console.error('Error listening to pets:', error);
              setLoading(false);
            });
      
          } catch (error) {
            console.error('Error initializing listener:', error);
            setLoading(false);
          }
        };
      
        fetchActivePets();
      
        // Cleanup listener khi component unmount
        return () => {
          if (unsubscribe) unsubscribe();
        };
    }, []);

    useEffect(() => {
        let unsubscribePet: () => void;
        let unsubscribeOwner: () => void;
    
        const fetchOwnerInfo = async (petId: string) => {
            try {
                
                const petRef = doc(db, 'pets', petId);
                unsubscribePet = onSnapshot(petRef, (petDoc: DocumentSnapshot<DocumentData>) => {
                    if (!petDoc.exists()) {
                        console.error("Pet document not found!");
                        return;
                    }
    
                    const petData = petDoc.data();
                    const ownerId = petData?.ownerId;
                    
                    if (!ownerId) {
                        console.error("Owner ID not found in pet document");
                        return;
                    }
                        
                    const ownerRef = doc(db, 'owners', ownerId);
                    unsubscribeOwner = onSnapshot(ownerRef, (ownerDoc: DocumentSnapshot<DocumentData>) => {
                        if (!ownerDoc.exists()) {
                            console.error("Owner document not found!");
                            return;
                        }
    
                        const ownerData = ownerDoc.data();
                        
                        setOwnerInfo({
                            fullName: ownerData?.fullName || "",
                            phone: ownerData?.phone || ""
                        });
                        
                        setFormData(prev => ({
                            ...prev,
                            ownerEmail: ownerData?.email,
                            phone: ownerData?.phone,
                            ownerId: ownerId,
                        }));
                    });
                });
            } catch (error) {
                console.error("Error fetching owner info:", error);
            }
        };
    
        if (formData.petId) {
            fetchOwnerInfo(formData.petId);
        }
    
        return () => {
            unsubscribePet?.();
            unsubscribeOwner?.();
        };
    }, [formData.petId]);

    const handleBeforeUpload = (file: RcFile) => {
        const fileExtension = file.name.split('.').pop()?.toLowerCase();
        const acceptedFileExtensions = ['png', 'jpg', 'jpeg', 'pdf', 'csv', 'doc', 'docx', 'pptx', 'ppt', 'txt', 'xls', 'xlsx'];
        if (fileExtension && !acceptedFileExtensions.includes(fileExtension)) {
            message.error(`File type not supported: ${fileExtension}`);
            return false;
        } else {
            setFileList([...fileList, file]);
            return false;
        }
    };

    const handleRemoveFile = (file: UploadFile<any>) => {
        const updatedFileList = fileList.filter((item) => item.uid !== file.uid);
        setFileList(updatedFileList);
    };

    //set Mobile, Cost Center, Total passengers, Pick location, Destination, Reason values ‚Äã‚Äãfor formdata
    const handleInputChange = (event: ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));
    };

    //set User values ‚Äã‚Äãfor formdata
    const handleSelectChange = (value: any, field: string) => {
        if (field === 'notificationType') {
            setFormData(prev => ({
              ...prev,
              notificationType: value // Gi√° tr·ªã ƒë√£ l√† chu·ªói tr·ª±c ti·∫øp
            }));
        }
        if (field === 'petId') {
            const petIdValue = value.value; // L·∫•y gi√° tr·ªã th·ª±c t·ª´ object
            setFormData((prevFormData) => ({
                ...prevFormData,
                [field]: petIdValue,
            }));
        } 
        else {
            setFormData((prevFormData) => ({
                ...prevFormData,
                [field]: value,
            }));
        }
    };

    //set Usage time from, Usage time to, Pick time values ‚Äã‚Äãfor formdata
    const handleDatePicker = (value: Dayjs | null, field: string) => {
        if (value) {
            const formattedValue = value.format('YYYY-MM-DDTHH:mm:ss');
            setFormData((prevFormData) => ({
                ...prevFormData,
                [field]: formattedValue,
            }));
        }
    };

    return (
        <RequestLayout profile={profile}>
            {() => (
                <div className='page-request'>
                    <MenuAdd formData={formData} setFormData={setFormData} fileList={fileList} setFileList={setFileList} setLoading={setLoading}  listOfUserId={listOfUserId}/>
                    <div className='page-content'>
                        {loading
                            ?
                            (<Spin style={{ height: '100vh' }} tip="Loading..." size="large">
                                <Alert
                                    style={{ width: '100%', textAlign: 'center' }}
                                    message="Loading..."
                                    description="Vui l√≤ng ch·ªù trong gi√¢y l√°t. ƒê·ª´ng t·∫£i l·∫°i trang."
                                    type="info"
                                />
                            </Spin>)
                            :
                            (
                                <>
                                    <div className='table-request'>
                                        <h2 className='title-request'>Y√äU C·∫¶U QU·∫¢N L√ù TH√ö C∆ØNG</h2>
                                        <div className='table-content'>
                                            <Form
                                                className='form-add-request'
                                            >
                                                <Row className='row-request'>
                               
                                                    {/* Notification Type */}
                                                    <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                        <Form.Item
                                                            label="Lo·∫°i th√¥ng b√°o"
                                                            name="notificationType"
                                                            rules={[{ required: true, message: "Vui l√≤ng ch·ªçn lo·∫°i th√¥ng b√°o" }]}
                                                            labelCol={{ span: 24 }}
                                                        >
                                                            <Select
                                                                showSearch
                                                                optionFilterProp="children"
                                                                filterOption={(input, option) =>
                                                                    (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                                                                }
                                                                onChange={(selectedOption) => handleSelectChange(selectedOption, 'notificationType')}
                                                                placeholder='Vui l√≤ng ch·ªçn lo·∫°i th√¥ng b√°o'
                                                                options={notificationOptions}
                                                                className='responsive-select-option'
                                                            >
                                                            {notificationOptions.map(option => (
                                                                <Select.Option 
                                                                    key={option.value} 
                                                                    value={option.value}
                                                                >
                                                                    {option.label}
                                                                </Select.Option>
                                                            ))}
                                                            </Select>
                                                        </Form.Item>
                                                    </Col>

                                                    {formData.notificationType === 'violation' && (
                                                        <>
                                                            {/*Info Pet*/}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="M√£ s·ªë th√∫ c∆∞ng"
                                                                    name="petId"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: "M√£ s·ªë th√∫ c∆∞ng l√† b·∫Øt bu·ªôc",
                                                                        },
                                                                    ]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Select
                                                                        labelInValue
                                                                        showSearch
                                                                        loading={loading}
                                                                        onChange={(value) => handleSelectChange(value, 'petId')}
                                                                        optionFilterProp="label"
                                                                        filterOption={(input, option) => 
                                                                            (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                                                                        }
                                                                        placeholder="Vui l√≤ng ch·ªçn m√£ s·ªë th√∫ c∆∞ng"
                                                                        options={petOptions}
                                                                        className='responsive-select-option'
                                                                        >
                                                                        {petOptions.map(option => (
                                                                            <Select.Option 
                                                                            key={option.value} 
                                                                            value={option.value}
                                                                            label={option.label}
                                                                            >
                                                                            {option.label}
                                                                            </Select.Option>
                                                                        ))}
                                                                    </Select>
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Owner Information */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                    <Form.Item
                                                                        required
                                                                        label="H·ªç v√† t√™n ch·ªß nu√¥i"
                                                                        labelCol={{ span: 24 }}
                                                                    >
                                                                        <Input 
                                                                            value={ownerInfo.fullName}
                                                                            readOnly
                                                                            placeholder='T·ª± ƒë·ªông ƒëi·ªÅn khi ch·ªçn m√£ th√∫ c∆∞ng'
                                                                        />
                                                                    </Form.Item>
                                                            </Col>

                                                            {/*Request Mobile*/}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    required
                                                                    label="S·ªë ƒëi·ªán tho·∫°i"
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        value={ownerInfo.phone}
                                                                        readOnly
                                                                        placeholder='T·ª± ƒë·ªông ƒëi·ªÅn khi ch·ªçn m√£ th√∫ c∆∞ng'
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Violation Location */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="ƒê·ªãa ƒëi·ªÉm vi ph·∫°m"
                                                                    name="violationLocation"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: "ƒê·ªãa ƒëi·ªÉm vi ph·∫°m l√† b·∫Øt bu·ªôc",
                                                                        },
                                                                    ]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        placeholder='Vui l√≤ng nh·∫≠p ƒë·ªãa ƒëi·ªÉm vi ph·∫°m'
                                                                        type='text' 
                                                                        name='violationLocation' 
                                                                        onChange={handleInputChange} 
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Violation Time */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="Th·ªùi gian vi ph·∫°m"
                                                                    name="violationTime"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: "Th·ªùi gian vi ph·∫°m l√† b·∫Øt bu·ªôc",
                                                                        },
                                                                    ]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <DatePicker
                                                                        className='add-request-width-formitem'
                                                                        onChange={(value) => handleDatePicker(value, 'violationTime')}
                                                                        showTime
                                                                        format="DD/MM/YYYY HH:mm"
                                                                        placeholder="Th·ªùi gian vi ph·∫°m"
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Description */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="Mi√™u t·∫£ vi ph·∫°m"
                                                                    name="description"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: "Vui l√≤ng nh·∫≠p mi√™u t·∫£ vi ph·∫°m",
                                                                        },
                                                                    ]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        type='text' 
                                                                        name="description" 
                                                                        onChange={handleInputChange}
                                                                        placeholder='Vui l√≤ng nh·∫≠p m√¥ t·∫£ vi ph·∫°m'
                                                                    >
                                                                    </Input>
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Notes */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="Ghi ch√∫"
                                                                    name="notes"
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        type='text' 
                                                                        name="notes" 
                                                                        onChange={handleInputChange}
                                                                        placeholder='Vui l√≤ng nh·∫≠p ghi ch√∫'
                                                                    >
                                                                    </Input>
                                                                </Form.Item>
                                                            </Col>

                                                            <Col span={24} className='col-request'>
                                                                <div className='Attachment'>
                                                                    <b>H√¨nh ·∫£nh minh ch·ª©ng</b>
                                                                </div>
                                                            </Col>
                                                                
                                                            <Col span={24} className='col-request'>
                                                                <div className='reply-upload-comment' style={{ width: 'fit-content' }}>
                                                                    <Upload
                                                                        className='upload-attachment-addrequest'
                                                                        beforeUpload={(file) => {
                                                                            const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
                                                                            if (!isJpgOrPng) {
                                                                              message.error('Ch·ªâ cho ph√©p file ƒë·ªãnh d·∫°ng .JPG/.JPEG/.PNG!');
                                                                              return Upload.LIST_IGNORE;
                                                                            }
                                                                        
                                                                            const isLt10M = file.size / 1024 / 1024 < 10;
                                                                            if (!isLt10M) {
                                                                              message.error('K√≠ch th∆∞·ªõc file ph·∫£i nh·ªè h∆°n 10MB!');
                                                                              return Upload.LIST_IGNORE;
                                                                            }
                                                                        
                                                                            handleBeforeUpload(file); // ƒë·∫©y file v√†o danh s√°ch fileList
                                                                            return false; // ngƒÉn Antd t·ª± upload
                                                                        }}
                                                                        accept=".png,.jpg,.jpeg"                                                                        
                                                                        fileList={fileList}
                                                                        onRemove={handleRemoveFile}
                                                                        multiple={true}
                                                                    >
                                                                        <Button 
                                                                            icon={<UploadOutlined />}
                                                                            className='btn-attachment-comment'
                                                                        >
                                                                            <b>T·ªáp ƒë√≠nh k√®m</b>
                                                                        </Button>
                                                                        <span className='attention-upload-attachment'> (Ch·ªâ ch·∫•p nh·∫≠n file ·∫£nh .png, .jpg, .jpeg, t·ªëi ƒëa 10MB)</span>
                                                                    </Upload>
                                                                </div>
                                                            </Col>
                                                        </>
                                                    )}

                                                    {formData.notificationType === 'vaccination' && (
                                                        <>
                                                            {/* Vaccine Type */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    required
                                                                    label="Lo·∫°i vaccine"
                                                                    rules={[{ required: true, message: "Lo·∫°i vaccine l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        name="vaccineType"
                                                                        placeholder="VD: Vaccine ph√≤ng d·∫°i"
                                                                        type='text' 
                                                                        onChange={handleInputChange} 
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Vaccine address */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    required
                                                                    label="ƒê·ªãa ƒëi·ªÉm ti√™m"
                                                                    rules={[{ required: true, message: "ƒê·ªãa ƒëi·ªÉm ti√™m l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        name="vaccineLocation"
                                                                        placeholder="VD: Tr·∫°m th√∫ y ph∆∞·ªùng 5, Qu·∫≠n 10"
                                                                        type='text' 
                                                                        onChange={handleInputChange} 
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Cost */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="Chi ph√≠ (n·∫øu c√≥)"
                                                                    rules={[{ required: true, message: "Chi ph√≠ l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input
                                                                        name="cost"
                                                                        placeholder="VD: 150000 (VND)"
                                                                        type='number'
                                                                        onChange={handleInputChange}
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Vaccine From */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="T·ª´ ng√†y"
                                                                    name="vaccineFrom"
                                                                    rules={[{ required: true, message: "Th·ªùi gian l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <DatePicker
                                                                        className='add-request-width-formitem'
                                                                        onChange={(value) => handleDatePicker(value, 'vaccineFrom')}
                                                                        showTime
                                                                        format="DD/MM/YYYY HH:mm"
                                                                        placeholder="Th·ªùi gian t·ª´ l√∫c"
                                                                    />
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Vaccine To */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="ƒê·∫øn ng√†y"
                                                                    name="vaccineTo"
                                                                    rules={[{ required: true, message: "Th·ªùi gian l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <DatePicker
                                                                        className='add-request-width-formitem'
                                                                        onChange={(value) => handleDatePicker(value, 'vaccineTo')}
                                                                        showTime
                                                                        format="DD/MM/YYYY HH:mm"
                                                                        placeholder="Th·ªùi gian ƒë·∫øn l√∫c"
                                                                    />
                                                                </Form.Item>
                                                            </Col>
                                                            
                                                            {/* Notes */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    label="Ghi ch√∫"
                                                                    name="notes"
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input 
                                                                        type='text' 
                                                                        name="notes" 
                                                                        onChange={handleInputChange}
                                                                        placeholder='Vui l√≤ng nh·∫≠p ghi ch√∫'
                                                                    >
                                                                    </Input>
                                                                </Form.Item>
                                                            </Col>

                                                            {/* Issuing Agency */}
                                                            <Col xs={24} sm={24} md={12} lg={8} xl={6} className='col-request'>
                                                                <Form.Item
                                                                    required
                                                                    label="C∆° quan ban h√†nh"
                                                                    rules={[{ required: true, message: "C∆° quan ban h√†nh l√† b·∫Øt bu·ªôc" }]}
                                                                    labelCol={{ span: 24 }}
                                                                >
                                                                    <Input
                                                                        name="issuingAuthority"
                                                                        placeholder="VD: Trung t√¢m th√∫ y Qu·∫≠n 10"
                                                                        type='text'
                                                                        onChange={handleInputChange}
                                                                    />
                                                                </Form.Item>
                                                            </Col>
                                                        </>
                                                    )}
                                                </Row>
                                            </Form>
                                        </div>
                                    </div>
                                    {formData.notificationType === 'vaccination' && (
                                        <>
                                            <h3>G·ª≠i ƒë·∫øn: </h3>
                                            <SendApprover departmentId={formData.DepartmentId} fileList={fileList} setFileList={setFileList} applyNote={applyNote} setApplyNote={setApplyNote} listOfUserId={listOfUserId} setListOfUserId={setListOfUserId} />
                                        </>
                                    )}
                                </>
                            )}

                    </div>
                </div>
            )
            }
        </RequestLayout >
    );
}

export default AddRequest;